import { store } from './../index';

export function action_duyrular() {
    return async () => {
        await fetch("https://ytuce-alan.herokuapp.com/duyurular").then((response) => {
            response.json().then((duyurular) => {
                console.log(duyurular)
                store.dispatch({
                    type: 'DUYURU',
                    payload: duyurular
                });
            });
        }).catch(function (err) {
            console.log('HatalÄ± Request', err);
        });

    }
}

