const initialState = {
    duyurular: [],
    teachers: [],
    teacher: [],
    searchText: '',

};



export default function reducer1(state = [], action) {
    switch (action.type) {

        case "DUYURU":

            return action.payload

        case "TEACHERS":

            return action.payload

        case "TEACHER":

            return  action.payload
            
        default:
            return state;

    }
}
